import React, {forwardRef} from "react"
import { createUseStyles } from "react-jss"
import {Carousel} from "react-bootstrap"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import SubTitle from "./shared/subtitle"

const collectionMetaData = {
  "image20": {
    title: "Flowers for Algernon",
    description: "The first piece I've ever made"
  },
  "image7": {
    title: "Flower banner",
  }
}

const Collection = forwardRef(({ onCollectionJump }, ref) => {
  const classes = useStyles()
  const data = useStaticQuery(graphql`
    query {
      lightboxImage: allFile(
        filter: {
          relativePath: { regex: "/image/" }
          sourceInstanceName: { eq: "images" }
        }
      ) {
        edges {
          node {
            childImageSharp {
              fluid {
                originalName
                ...GatsbyImageSharpFluid
                ...GatsbyImageSharpFluidLimitPresentationSize
              }
            }
          }
        }
      }
    }
  `)

  return (
    <div ref={ref} className={classes.root}>
      <SubTitle text="Collection" />

      <Carousel>
        {data.lightboxImage.edges.map((image, index) => (
          <Carousel.Item
            key={image.node.id}
            style={{
              background: "black",
              height: "80vh",
            }}
          >
            <div
              style={{
                height: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: "#f2f2f2",
              }}
            >
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",

                  //   height: "300px",

                  width: `min(80vw,${
                    image.node.childImageSharp.fluid.aspectRatio * 80
                  }vh)`,
                  //   margin: 'auto'
                  //   height: "1000px",
                }}
              >
                <Img
                  fluid={image.node.childImageSharp.fluid}
                  imgStyle={{ objectFit: "INSIDE" }}
                  style={{
                    display: "flex",
                    width: "100%",
                    //   margin: "0px auto"
                  }}
                />
              </div>
            </div>
            <Carousel.Caption>
              <h4 className={classes.caption}>
                {image.node.childImageSharp.fluid.originalName.slice(0, -4) in
                collectionMetaData
                  ? collectionMetaData[
                      image.node.childImageSharp.fluid.originalName.slice(0, -4)
                    ].title
                  : image.node.childImageSharp.fluid.originalName.slice(0, -4)}
              </h4>
              <p>
                {image.node.childImageSharp.fluid.originalName.slice(0, -4) in
                  collectionMetaData &&
                  collectionMetaData[
                    image.node.childImageSharp.fluid.originalName.slice(0, -4)
                  ]?.description}
              </p>
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>
    </div>
  )
})

const useStyles = createUseStyles({
  root: {
    margin: "auto",
    marginBottom: "20px",
    display: "flex",
    flexFlow: "column",
    width: "80%",
    // maxWidth: "1200px",
  },
  imageContainer: {
    display: "flex",
    margin: "50px 50px",
    width: "300px",
    height: "300px",
    /* background-color: #e0e0e0; */
    alignItems: "center",
    justifyContent: "center",
    "&:hover": {
      transform:
        "scale(1.2)" /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */,
      transitionDuration: "500ms",
    },
  },
  caption: {
    textShadow: "2px 2px #000000",
    margin:'auto',
    padding: '5px',
    width:'fit-content',
    backgroundColor: "rgba(0,0,0,0.6)"
  },
})

export default Collection
